.item {
  position: relative;
}

.image {
  width: 150px;
  height: 150px;
  margin: 0.1rem 0.25rem;
}

.image-footer {
  display: flex;
  padding-top: 0.1rem;
  background: rgb(0 0 0 / 54%);
  height: 27px;
  position: absolute;
  width: 150px;
  z-index: 10;
  /* background: black; */
  left: 4px;
  bottom: 5px;
  justify-content: space-around;
}

.rotate-icon,
.expand-icon,
.delete-icon {
  cursor: pointer;
  /* width: 20px;
  margin-left: 1rem; */
}
