.App {
  display: flex;
  /* text-align: center; */
  flex-direction: column;
  justify-content: revert;
  align-items: center;
  margin: 1rem 0.3rem 1rem 0.3rem;
}

.header {
  display: flex;
  align-items: center;

  justify-content: space-between;
}
.drag-btn {
  margin-top: -20px;
  border-radius: 5px;
  outline: none;
  border: none;
  padding: 0.5rem;

  cursor: pointer;
  margin-right: 2rem;
  width: 55px;
}
.draggable-btn {
  background: rgb(52, 174, 52);
}
.not-draggable-btn {
  background: rgb(241, 89, 89);
}
